'use client';

import { useEffect, useState } from 'react';
import Link from 'next/link';

export default function AiMatchingPage() {
    const [progress, setProgress] = useState(0);

    useEffect(() => {
        const interval = setInterval(() => {
            setProgress((prev) => {
                if (prev >= 100) {
                    return 0; // Reset progress to create continuous search animation
                }
                return prev + 2; // Slower progress
            });
        }, 150);

        return () => clearInterval(interval);
    }, []);

    return (
        <div className="min-h-screen bg-black text-white flex flex-col">
            {/* Status Bar */}
            <div className="flex justify-between items-center p-4 pt-12 text-sm">
                <span>9:41</span>
                <div className="flex items-center gap-1">
                    <div className="flex gap-1">
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                        <div className="w-1 h-1 bg-white rounded-full"></div>
                    </div>
                    <svg className="w-4 h-4" viewBox="0 0 24 24" fill="white">
                        <path d="M2 17h20v2H2v-2zm1.15-6.05L4 9.47l.85 1.48H5c.55 0 1 .45 1 1s-.45 1-1 1-.45-.45-.45-1zm5.5 2.5H9c.55 0 1-.45 1-1s-.45-1-1-1-.45.45-.45 1z" />
                    </svg>
                    <div className="w-6 h-3 border border-white rounded-sm">
                        <div className="w-4 h-1.5 bg-white rounded-sm m-0.5"></div>
                    </div>
                </div>
            </div>

            {/* Header */}
            <div className="text-center px-8 mb-16">
                <h1 className="text-4xl font-bold mb-2">Pickom</h1>
                <p className="text-gray-400 text-lg">People-Powered Delivery</p>
            </div>

            {/* Main Content */}
            <div className="flex-1 flex flex-col items-center justify-center px-8 text-center">
                <h2 className="text-3xl font-bold mb-16">AI Matching</h2>

                {/* Robot/AI Icon */}
                <div className="mb-16 relative">
                    <div className="w-24 h-24 bg-orange-500 rounded-2xl flex items-center justify-center mb-4 animate-pulse">
                        <svg className="w-16 h-16 text-white" viewBox="0 0 24 24" fill="currentColor">
                            <path d="M12 2C13.1 2 14 2.9 14 4C14 5.1 13.1 6 12 6C10.9 6 10 5.1 10 4C10 2.9 10.9 2 12 2ZM21 9V7L15 1H5C3.9 1 3 1.9 3 3V21C3 22.1 3.9 23 5 23H19C20.1 23 21 22.1 21 21V9M19 21H5V3H13V9H19V21Z" />
                        </svg>
                    </div>

                    {/* Circuit pattern around icon */}
                    <div className="absolute -top-4 -left-4 w-32 h-32 border-2 border-orange-500 rounded-3xl opacity-30 animate-spin-slow"></div>
                    <div className="absolute -top-8 -left-8 w-40 h-40 border border-orange-300 rounded-full opacity-20 animate-pulse"></div>
                </div>

                <p className="text-xl text-gray-300 mb-8">Finding the best travelers for this route...</p>

                {/* Progress Bar */}
                <div className="w-64 h-2 bg-gray-700 rounded-full mb-8">
                    <div
                        className="h-full bg-white rounded-full transition-all duration-300 ease-out"
                        style={{ width: `${progress}%` }}
                    ></div>
                </div>

                {/* Animated dots */}
                <div className="flex space-x-1 mb-16">
                    <div className="w-2 h-2 bg-orange-500 rounded-full animate-bounce"></div>
                    <div className="w-2 h-2 bg-orange-500 rounded-full animate-bounce" style={{ animationDelay: '0.1s' }}></div>
                    <div className="w-2 h-2 bg-orange-500 rounded-full animate-bounce" style={{ animationDelay: '0.2s' }}></div>
                </div>
            </div>

            {/* Continue Button */}
            <div className="p-8">
                <Link
                    href="/select-traveler"
                    className="w-full bg-orange-500 text-white py-4 rounded-full text-lg font-medium text-center block hover:bg-orange-600 transition-colors"
                >
                    View Available Travelers
                </Link>
            </div>

            {/* Bottom Indicator */}
            <div className="flex justify-center pb-6">
                <div className="w-32 h-1 bg-white rounded-full"></div>
            </div>
        </div>
    );
} 